$vert_fonce: #416c25;
$vert: #79b51d;
$jaune: #f6cc08;

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

@mixin text_color_center($color) {
  color: $color;
  text-align: center;
  a {
    color: inherit;
  }
}

%img_100 {
  img {
    width: 100%;
  }
}

.filtre_vert {
  background-color: $vert_fonce;
  @include text_color_center(#fff);
}

.menu {
  display: flex;
  justify-content: space-around;
  font-size: 0.8rem;
}

.entree_menu {
  padding: 10px;
}

.bandeau_menu {
  @extend .menu;
  @include text_color_center(#000);
  .image_ab_bandeau {
    flex-grow: 0;
  }
  div {
    flex-grow: 1;
  }
}

.image_ab_bandeau img {
  height: 28px;
  position: absolute;
  transform: translateX(-50%);
}

.cadre_vert_menu, .cadre_vert {
  background-color: $vert;
  min-width: 50%;
}

.cadre_jaune_menu, .cadre_jaune {
  background-color: $jaune;
  min-width: 50%;
}

.img_fond {
  @extend %img_100;
}

.separateur {
  height: 30px;
}

.row {
  display: flex;
  margin: 10px;
}

.row_bandes {
  height: 20px;
  margin: 0;
  div {
    width: 50%;
  }
}

.actus {
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.img-actu {
  width: 40%;
  margin-right: 20px;
  @extend %img_100;
}

.texte_actu {
  width: 60%;
}

@mixin bandeau-texte-couleur($cat) {
  height: 30px;
  background-image: url("../img/texte-" + $cat + ".png");
  background-blend-mode: soft-light;
  background-repeat: repeat-x;
}

.img-texte-legumes {
  @include bandeau-texte-couleur('legumes-de-saison');
}

@mixin producteur($cat, $couleur, $var_couleur) {
  margin: 0 30px;
  @extend %img_100;
  .cadre_#{$couleur}_titre {
    text-align: center;
    background-color: $var_couleur;
  }
  .img-producteur {
    margin: 20px 0;
  }
  .slide-#{$cat} {
    @include text_color_center(#fff);
    background-color: $var_couleur;
    font-size: 1.5rem;
    padding: 10px 0;
  }
}

.producteur_legumes {
  @include producteur(legumes, vert, $vert);
}

.img-texte-pain {
  @include bandeau-texte-couleur('pain-levain');
}

.producteur_pain {
  @include producteur(pain, jaune, $jaune);
}
